"use client"

import { use } from "react"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Badge } from "@/components/ui/badge"
import { getUsers, getPets, getBlogPosts, updateUser } from "@/lib/storage"
import { useAuth } from "@/lib/auth"
import { MapPin, Calendar, Users, Heart } from "lucide-react"
import Link from "next/link"
import { useState, useEffect } from "react"

export default function ProfilePage({ params }: { params: Promise<{ username: string }> }) {
  const { username } = use(params)
  const { user: currentUser } = useAuth()
  const [user, setUser] = useState(() => getUsers().find((u) => u.username === username))
  const [pets, setPets] = useState(() => (user ? getPets().filter((p) => p.ownerId === user.id) : []))
  const [posts, setPosts] = useState(() =>
    user
      ? getBlogPosts()
          .filter((p) => p.authorId === user.id)
          .slice(0, 6)
      : [],
  )
  const [isFollowing, setIsFollowing] = useState(false)

  useEffect(() => {
    if (currentUser && user) {
      setIsFollowing(user.followers.includes(currentUser.id))
    }
  }, [currentUser, user])

  const handleFollow = () => {
    if (!currentUser || !user) return

    const updatedUser = { ...user }
    const updatedCurrentUser = { ...currentUser }

    if (isFollowing) {
      updatedUser.followers = updatedUser.followers.filter((id) => id !== currentUser.id)
      updatedCurrentUser.following = updatedCurrentUser.following.filter((id) => id !== user.id)
    } else {
      updatedUser.followers.push(currentUser.id)
      updatedCurrentUser.following.push(user.id)
    }

    updateUser(updatedUser)
    updateUser(updatedCurrentUser)
    setUser(updatedUser)
    setIsFollowing(!isFollowing)
  }

  if (!user) {
    return (
      <div className="container mx-auto px-4 py-8">
        <p className="text-center text-muted-foreground">User not found</p>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-6xl">
      <Card>
        <CardHeader className="pb-4">
          <div className="flex flex-col md:flex-row gap-6 items-start">
            <Avatar className="h-24 w-24">
              <AvatarImage src={user.avatar || "/placeholder.svg"} alt={user.fullName} />
              <AvatarFallback>{user.fullName.charAt(0)}</AvatarFallback>
            </Avatar>
            <div className="flex-1 space-y-3">
              <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-3">
                <div>
                  <h1 className="text-3xl font-bold">{user.fullName}</h1>
                  <p className="text-muted-foreground">@{user.username}</p>
                </div>
                {currentUser && currentUser.id !== user.id && (
                  <Button onClick={handleFollow} variant={isFollowing ? "outline" : "default"}>
                    {isFollowing ? "Unfollow" : "Follow"}
                  </Button>
                )}
              </div>
              {user.bio && <p className="text-foreground">{user.bio}</p>}
              <div className="flex flex-wrap gap-4 text-sm text-muted-foreground">
                {user.location && (
                  <div className="flex items-center gap-1">
                    <MapPin className="h-4 w-4" />
                    {user.location}
                  </div>
                )}
                <div className="flex items-center gap-1">
                  <Calendar className="h-4 w-4" />
                  Joined {new Date(user.joinedAt).toLocaleDateString("en-US", { month: "long", year: "numeric" })}
                </div>
              </div>
              <div className="flex gap-4 text-sm">
                <div>
                  <span className="font-semibold">{user.followers.length}</span>{" "}
                  <span className="text-muted-foreground">Followers</span>
                </div>
                <div>
                  <span className="font-semibold">{user.following.length}</span>{" "}
                  <span className="text-muted-foreground">Following</span>
                </div>
                <div>
                  <span className="font-semibold">{pets.length}</span>{" "}
                  <span className="text-muted-foreground">{pets.length === 1 ? "Pet" : "Pets"}</span>
                </div>
              </div>
            </div>
          </div>
        </CardHeader>
      </Card>

      <Tabs defaultValue="pets" className="mt-6">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="pets">Pets</TabsTrigger>
          <TabsTrigger value="posts">Posts</TabsTrigger>
        </TabsList>
        <TabsContent value="pets" className="mt-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {pets.map((pet) => (
              <Link key={pet.id} href={`/pet/${pet.id}`}>
                <Card className="hover:shadow-lg transition-shadow cursor-pointer">
                  <CardContent className="p-4">
                    <div className="flex items-start gap-3">
                      <Avatar className="h-16 w-16">
                        <AvatarImage src={pet.avatar || "/placeholder.svg"} alt={pet.name} />
                        <AvatarFallback>{pet.name.charAt(0)}</AvatarFallback>
                      </Avatar>
                      <div className="flex-1 min-w-0">
                        <h3 className="font-semibold text-lg">{pet.name}</h3>
                        <p className="text-sm text-muted-foreground capitalize">
                          {pet.breed || pet.species}
                          {pet.age && ` • ${pet.age} ${pet.age === 1 ? "year" : "years"} old`}
                        </p>
                        {pet.bio && <p className="text-sm mt-2 line-clamp-2">{pet.bio}</p>}
                        <div className="flex items-center gap-1 mt-2 text-sm text-muted-foreground">
                          <Users className="h-3 w-3" />
                          {pet.followers.length} {pet.followers.length === 1 ? "follower" : "followers"}
                        </div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </Link>
            ))}
          </div>
          {pets.length === 0 && (
            <Card>
              <CardContent className="p-8 text-center text-muted-foreground">No pets yet</CardContent>
            </Card>
          )}
        </TabsContent>
        <TabsContent value="posts" className="mt-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {posts.map((post) => {
              const pet = pets.find((p) => p.id === post.petId)
              return (
                <Link key={post.id} href={`/blog/${post.id}`}>
                  <Card className="hover:shadow-lg transition-shadow cursor-pointer">
                    {post.coverImage && (
                      <div className="aspect-video w-full overflow-hidden rounded-t-lg">
                        <img
                          src={post.coverImage || "/placeholder.svg"}
                          alt={post.title}
                          className="w-full h-full object-cover"
                        />
                      </div>
                    )}
                    <CardContent className="p-4">
                      <h3 className="font-semibold text-lg line-clamp-2">{post.title}</h3>
                      <p className="text-sm text-muted-foreground mt-1">
                        By {pet?.name} • {new Date(post.createdAt).toLocaleDateString()}
                      </p>
                      <p className="text-sm mt-2 line-clamp-2">{post.content}</p>
                      <div className="flex items-center gap-4 mt-3 text-sm text-muted-foreground">
                        <div className="flex items-center gap-1">
                          <Heart className="h-4 w-4" />
                          {post.likes.length}
                        </div>
                        <div className="flex gap-1 flex-wrap">
                          {post.tags.slice(0, 3).map((tag) => (
                            <Badge key={tag} variant="secondary">
                              {tag}
                            </Badge>
                          ))}
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </Link>
              )
            })}
          </div>
          {posts.length === 0 && (
            <Card>
              <CardContent className="p-8 text-center text-muted-foreground">No posts yet</CardContent>
            </Card>
          )}
        </TabsContent>
      </Tabs>
    </div>
  )
}
